import {defineStore} from "pinia/dist/pinia";


export const storeProactive = defineStore('storeProactive',{
    state:(()=>{
        return {
            metrics:[]
        }
    }),
    actions:{
        async loadMainTable() {
            const mainTable = await new Promise((resolve => {
                BX.rest.callMethod("mwsproactive.ui.proactiveTableUi", {}, res => {resolve(res.data())})
            }))

            this.metrics = mainTable
        },


    },
    getters:{
        getMetrics(state){
            return state.metrics
        }
    }
})